import React, { Component } from 'react';
import { TweenLite } from "gsap/TweenMax";

export default class Sustainability extends Component {
  currActive = 0;
  currMove = 0;
  threshold = 0.2;
  momentum = 0.5;

  componentDidMount(){
    this.slider = document.querySelector(".sustainability-slider");
    this.slides = document.querySelectorAll(".sustainability-slide");
    this.navItems = document.querySelectorAll(".sustainability-nav-item");
    this.allVideos = document.querySelectorAll("video");
    this.slidePositions = [];
    this.windowW = window.innerWidth;

    this.slides.forEach((slide, i) => {
      slide.style.left = 100 * i + "vw";
    })

    this.navItems.forEach((item, i) => {
      this.slidePositions.push(i * this.windowW * -1);
    })

    document.querySelector(".burger").addEventListener("click", () => {
      setTimeout(() => {
        this.navClicked(0);
      }, 700)
    })
  }

  navClicked(i){
    this.navItems[this.currActive].classList.remove("-active");

    TweenLite.to(this.slider, 0.7, {x: this.slidePositions[i]});

    this.currMove = this.slidePositions[i];
    this.currActive = i;
    this.navItems[this.currActive].classList.add("-active"); 
  }

  down(e){
    if(this.windowW < 768) return;
    this.pressed = true;
    this.startX = e.touches[0].clientX;
    this.startMove = this.startX - this.currMove;

    this.allVideos.forEach(video => {
      // video.pause();
    })
  }

  up(){
    this.pressed = false;

    if(
      this.currActive < this.slidePositions.length - 1 && 
      this.currMove < this.slidePositions[this.currActive] - ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive++;
    } else if(
      this.currActive > 0 && 
      this.currMove > this.slidePositions[this.currActive] + ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive--;
    }

    this.currMove = this.slidePositions[this.currActive];
    this.navItems[this.currActive].classList.add("-active");
    TweenLite.to(this.slider, 0.7, {x: this.currMove, onComplete: () => {
      setTimeout(() => {
        this.allVideos.forEach(video => {
          // video.play();
        })
      }, 100)
    }});
  }

  move(e){
    if(!this.pressed) return;

    let x = e.touches[0].clientX,
        readjust = (this.startX - x) * this.momentum;

    x -= this.startMove;

    this.currMove = x - readjust;

    TweenLite.to(this.slider, 0, {x: this.currMove});
  }
  
  render(){
    const data = this.props.data;

    return(
      <section className="mainSection sustainability">
        <div className="sustainability-slider"
          onTouchStart={e => this.down(e)}
          onTouchEnd={() => this.up()}
          onTouchMove={e => this.move(e)}
        >
          <div className="sustainability-slide">
            <h2 className="heading fadeUp" dangerouslySetInnerHTML={{ __html: data.heading }}/>
            {/* <p className="sustainability-divider">—</p> */}
            <p className="paragraph sustainability-paragraphOne fadeUp" dangerouslySetInnerHTML={{ __html: data.para1 }}/>
            <img src="./images/sustainability/sustainability-1.jpg" alt="" className="mainSection-img sustainability-imgOne"/>
            <img src={`./images/sustainability/mark/${this.props.lang}.svg`} alt="" className="sustainability-mark"/>
            <a href="https://www.silverfernfarms.com/assets/Sustainability-Report-2018.pdf" target="_blank" rel="noopener noreferrer" className="sustainability-QR-link">
              <img src="./images/sustainability/QR_SustainabilityReport.jpg" alt="" className="sustainability-QR"/>
              <p className="sustainability-QR-text" dangerouslySetInnerHTML={{ __html: data.qrText }}/>
            </a>
            <video src="./videos/video3.mp4" className="mainSection-video sustainability-videoOne" poster="./images/placeholders/video3.png" autoPlay muted loop/>
          </div>

          <div className="sustainability-slide">
            <div className="paragraph sustainability-paragraphBlock fadeUp">
              {data.para2.map((para, i) => {
                return <p key={`para-${i}`} dangerouslySetInnerHTML={{ __html: para }}/>
              })}
            </div>
            
            <p className="paragraph sustainability-paragraphThree fadeUp"dangerouslySetInnerHTML={{ __html: data.para3 }}/>
            <img src="./images/sustainability/reserve-crop.jpg" alt="" className="mainSection-img sustainability-imgTwo"/>
            <img src={`./images/sustainability/oritain/${this.props.lang}.svg`} alt="" className="sustainability-oritain"/>
            <video src="./videos/video4.mp4" poster="./images/placeholders/video4.png" className="mainSection-video sustainability-videoTwo" autoPlay muted loop/>
          </div>

          <div className="sustainability-slide">
            <p className="paragraph sustainability-paragraphFour fadeUp" dangerouslySetInnerHTML={{ __html: data.para4 }}/>
            <video src="./videos/sustainability-big.mp4" poster="./images/placeholders/sustainability-big.png" className="mainSection-video sustainability-videoThree" autoPlay muted loop/>

            <img src="./images/landing/MadeOfNZ.svg" alt="" className="sustainability-madeOfNZ"/>
          </div>
        </div>

        <div className="sustainability-nav">
          {data.navItems.map((item, i) => {
            return(
              <div key={`sustain-item-${i}`} className={`sustainability-nav-item ${i === 0 ? "-active" : ""}`} style={{borderColor: "#000"}} onClick={() => this.navClicked(i)}>{item}</div>
            )
          })}
        </div>
      </section>
    )
  }
}


