import React, { Component } from 'react';
import Squeegie from './Squeegie';
import { TweenLite } from "gsap/TweenMax";

export default class Meat extends Component {
  btmLeftActive = 0;
  timing = 16;
  bottomLefts = false;

  setupScaling(el){
    if(el === null || this.bottomLefts !== false) return;

    this.bottomLefts = el.querySelectorAll(".meat-bottom-left-img");

    this.loopThrough();
  }

  loopThrough(){
    TweenLite.to(this.bottomLefts[this.btmLeftActive], 1, {opacity: 1});

    TweenLite.fromTo(this.bottomLefts[this.btmLeftActive], this.timing, {scale: 1}, {scale: 1.4, onComplete: () => {
      let fadeOut = this.bottomLefts[this.btmLeftActive]; 

      TweenLite.to(fadeOut, 1, {opacity: 0, onComplete: () => {
        TweenLite.to(fadeOut, 0, {scale: 1});
      }});

      if(this.btmLeftActive < this.bottomLefts.length - 1){
        this.btmLeftActive++;
      } else {
        this.btmLeftActive = 0;
      }

      this.loopThrough();
    }})
  }

  info(){
    const infoText = this.props.data.info;

    return(
      <div className="squeegie-info">
        {infoText.map((text, i) => {
          return(
            <p key={`info-${i}`} className="squeegie-info-text">{text}</p>
          )
        })}
      </div>
    )
  }
  
  render(){
    const data = this.props.data;

    return(
      <section className={`meat -${data.type} -active`}>
        <div className="meat-background"></div>
        <img src={`./images/titles/${data.type}/${this.props.lang}.svg`} alt="" className="meat-titleImg fadeUp"/>
        <div className="meat-top">
          <Squeegie 
            data={{classes: "meat-top-squeegie"}}
            expandable={true}
            info={this.props.data.info ? this.props.data.info : false }
          >
            <div className="squeegie-background">
              {data.mountains.map(mountain => {
                return <img key={mountain} className="squeegie-background-mountain" src={mountain}/>  
              })}
            </div>
            <div className="squeegie-slide" style={{backgroundImage: `url(${data.old})`}}/>
            <div className="squeegie-slide" style={{backgroundImage: `url(${data.new})`}}/>
          </Squeegie>
        </div>
        <div className="meat-bottom">
          <div className="meat-bottom-left">
            <h1 className="meat-bottom-left-title fadeUp">{data.title}</h1>
            <div className="meat-bottom-left-img" ref={this.setupScaling.bind(this)}>
              {data.bottomImgs.map(bottomImg => {
                return <img key={bottomImg} src={bottomImg} alt="" className="meat-bottom-left-img"/>
              })}
            </div>
          </div>
          <div className="meat-bottom-right fadeUp">
            {data.paragraphs.map((paragraph, i) => {
              return <p className="meat-bottom-right-paragraph" key={`para-${i}`} dangerouslySetInnerHTML={{__html: paragraph}}/>
            })}
          </div>
        </div>
        <div className="meat-icons">
          {data.icons.map(icon => {
            return <img key={icon} src={icon} alt="" className="meat-icons-img fadeUp"/>
          })}
        </div>

        {this.info()}
      </section>
    )
  }
} 


