import React, { Component } from 'react';

import Landing from './components/Landing';
import Nav from './components/Nav';
import NewPackaging from './components/NewPackaging';
import Source from './components/Source';
import Sustainability from './components/Sustainability';
import Meats from './components/Meats';

class App extends Component {
  state = {
    langData: false,
    showMeats: false
  }

  languages = [
    "chn",
    "deu",
    "ara",
    "nld"
  ]

  langInRoute = "eng";

  constructor(){
    super();

    this.checkStorage();

    let windowW = window.innerWidth;

    if(windowW > 1024){
      let windowH = window.innerHeight * ( 768 / 1024);

      document.documentElement.style.setProperty('--maxedVW', windowH / 100 +"px");
    }

    document.querySelector(".stuck").classList.remove("-active");
  }

  componentDidMount(){
    const path = this.props.location.pathname,
          splitPath =  path.includes("/") && path.length > 1 ? path.split("/") : false;

    this.langInRoute = splitPath !== false ? splitPath[splitPath.length - 1] : "eng";

    let checkLanguageAvilable = this.languages.find(this.checkLang.bind(this));

    if(checkLanguageAvilable === undefined) checkLanguageAvilable = "eng";

    const manifest = document.getElementById("manifest");
    manifest.setAttribute("href",`./manifest-${checkLanguageAvilable}.json`);

    this.getLang(checkLanguageAvilable);
  }

  checkStorage(){
    if ('storage' in navigator && 'estimate' in navigator.storage) {
      navigator.storage.estimate().then(({usage, quota}) => {
        console.log(`Using ${usage} out of ${quota} bytes.`);
      }).catch(error => {
        console.error('Loading storage estimate failed:');
        console.log(error.stack);
      });
    } else {
      console.error('navigator.storage.estimate API unavailable.');
    }
  }

  addMutedToVideos(){
    this.videos = document.querySelectorAll("video");

    console.log('this.videos',this.videos);

    const muteVideo = video => {
      let mutedVideo = video,
          parent = video.parentNode;

      mutedVideo.setAttribute("muted","muted");
      mutedVideo.setAttribute("playsinline","playsinline");
      
      video.remove();
      parent.append(mutedVideo);
    }
    
    this.videos.forEach(video => {
      let checkLoaded = setTimeout(() => {
        muteVideo(video);
      }, 2000);

      video.onloadeddata = () => {
        // this.debugThemVids(video);
        clearTimeout(checkLoaded);
        muteVideo(video);
      }
    })
  }

  debugThemVids(video){
    console.log('video.muted',video.muted);
    console.log('video.networkState',video.networkState);

    video.addEventListener('abort', e => {
      console.log('video.networkState',video.networkState);
      console.log('e',e);
    });

    video.addEventListener('error', e => {
      console.log('video.error',video.error);
      console.log('e',e);
    });

    video.addEventListener('loadstart', e => {
      console.log('loadstarted',e);
    });

    video.addEventListener('progress', e => {
      console.log('progress',e);
    });
  } 

  checkLang(check){
    return check === this.langInRoute
  }

  getLang(lang){
    if(lang === "chn"){
      // WebFont.load({
      //   google: {
      //     families: ['Noto Serif SC:200,700,900:latin,chinese-simplified', 'serif']
      //   }
      // });

      document.querySelector("script").insertAdjacentHTML('afterend', `
        <style>
          @font-face{
            src: url('./onlineOnly/NotoSerifSC-Black.otf');
            font-family: NotoSerifSC-Black;
          }

          @font-face{
            src: url('./onlineOnly/NotoSerifSC-Bold.otf');
            font-family: NotoSerifSC-Bold;
          }

          @font-face{
            src: url('./onlineOnly/NotoSerifSC-ExtraLight.otf');
            font-family: NotoSerifSC-ExtraLight;
          }
        </style>
      `)
    }

    fetch(`./lang/${lang}.json`)
      .then(r => r.json())
      .then(data => {
        this.setState({langData: data});
        this.init();
      });
  }

  init(){
    this.slides = document.querySelectorAll("section");
    this.allLayers = document.querySelector("main").childNodes;
    this.addFadeUps();

    document.addEventListener("orientationchange", function(event){
      switch(window.orientation) 
      {  
          case -90: case 90:
              break; 
              
          default:
      }
    });

    this.addMutedToVideos();
  }

  addFadeUps(){
    this.fadeUps = [];

    this.slides.forEach(slide => {
      slide.querySelectorAll(".fadeUp").forEach((fadeUp, i) => {
        fadeUp.style.transitionDelay = 0.15 * (i + 1) + "s";
      })
    })
  }

  burgerClicked(){
    this.allLayers.forEach(layer => {
      layer.classList.remove("-active");
    })

    document.querySelector(".nav").classList.add("-active");
  }

  showMeats(i){
    this.setState({
      showMeats: i
    })

    document.querySelector(".nav").classList.remove("-active");
    document.querySelector(".burger").classList.add("-active");
    document.querySelector(".meats").classList.add("-active");
    document.querySelector(".meats-nav").classList.add("-active");
  }

  hideMeats(){
    this.setState({
      showMeats: false
    })
  }

  render() {
    const langData = this.state.langData;

    return (
      <main className={"-lang-"+langData.lang}>
        {langData !== false ? (
          <React.Fragment>
            <Landing data={langData.Landing}/>
            <img src="./images/Burger.svg" alt="" className="burger" onClick={() => this.burgerClicked()}/>
            <Nav showMeats={this.showMeats.bind(this)} data={langData.Nav}/>
            <NewPackaging data={langData.NewPackaging}/>
            <Source data={langData.Source}/>
            <Sustainability data={langData.Sustainability} lang={langData.lang}/>
            <Meats data={langData.Meats} lang={langData.lang} currActive={this.state.showMeats}/>
          </React.Fragment>
        ) : null}
      </main>
    );
  }
}

export default App;


