import React, { Component } from 'react';

export default class Landing extends Component {
  timing = 700;
  eventListenClose;

  componentDidMount(){
    this.front = document.querySelector(".landing-front");
    this.nav = document.querySelector(".nav");
    this.landing = document.querySelector(".landing");
    this.madeOfNZ = document.querySelector(".landing-madeOfNZ");
    this.navLogo = document.querySelector(".nav-sffLogo");

    this.navLogo.addEventListener("click", () => {
      this.fadeDown();
    })
  }

  fadeUp(){
    this.madeOfNZ.classList.remove("-active");
    this.front.classList.add("-hide");

    setTimeout(() => {
      this.nav.classList.add("-active");

      setTimeout(() => {
        this.landing.classList.remove("-active");
        document.querySelector(".nav-close").classList.add("-backToStart");
      }, this.timing)
    }, this.timing)
  }

  fadeDown(){
    setTimeout(() => {
      this.landing.classList.add("-active");
      this.nav.classList.remove("-active");

      setTimeout(() => {
        this.front.classList.remove("-hide");

        setTimeout(() => {
          this.madeOfNZ.classList.add("-active");
        }, this.timing)
      }, this.timing)
    }, this.timing / 2)
  }

  reload(){
    window.location.reload();
  }

  render(){
    const data = this.props.data;

    return(
      <section className="mainSection landing -active" onClick={() => this.fadeUp()}>
        {/* <p className="landing-checkUpdates" onClick={() => this.reload()}>Check for updates</p> */}
        <a className="landing-checkUpdates" href="?">{data.check} <span>{data.updates}</span></a>
        <div className="landing-back-img" style={{backgroundImage: "url('./images/landing/landing-back.svg')"}}/>
        <div className="landing-front">
          <div className="landing-front-img" style={{backgroundImage: "url('./images/landing/landing-front.svg')"}}/>
        </div>

        <h3 className="landing-heading">{data.title}</h3>
        <div className="landing-line"></div>

        <img src="./images/landing/SFF_Logo_Black.svg" alt="" className="landing-sffLogo"/>
        <img src="./images/landing/MadeOfNZ.svg" alt="" className="landing-madeOfNZ -active"/>
      </section>
    )
  }
}


