import React, { Component } from 'react';
import { TweenLite, Power0 } from "gsap/TweenMax";

export default class NewPackaging extends Component {
  currActive = 0;
  currMove = 0;
  threshold = 0.2;
  momentum = 0.5;

  timing = 60;

  componentDidMount(){
    this.slider = document.querySelector(".newPackaging-slider");
    this.slides = document.querySelectorAll(".newPackaging-slide");
    this.navItems = document.querySelectorAll(".newPackaging-nav-item");
    this.allVideos = document.querySelectorAll("video");
    this.slidePositions = [];
    this.windowW = window.innerWidth;

    this.slideShow = document.querySelector(".newPackaging-backgroundSlideShow");

    this.slides.forEach((slide, i) => {
      slide.style.left = 100 * i + "vw";
    })

    this.navItems.forEach((item, i) => {
      this.slidePositions.push(i * this.windowW * -1);
    })

    document.querySelector(".burger").addEventListener("click", () => {
      setTimeout(() => {
        this.navClicked(0);
      }, 700)
    })

    // this.loopThroughProducts();
  }

  navClicked(i){
    this.navItems[this.currActive].classList.remove("-active");

    TweenLite.to(this.slider, 0.7, {x: this.slidePositions[i]});

    this.currMove = this.slidePositions[i];
    this.currActive = i;
    this.navItems[this.currActive].classList.add("-active"); 
  }

  down(e){
    if(this.windowW < 768) return;
    this.pressed = true;
    this.startX = e.touches[0].clientX;
    this.startMove = this.startX - this.currMove;

    this.allVideos.forEach(video => {
      // video.pause();
    })
  }

  up(){
    this.pressed = false;

    if(
      this.currActive < this.slidePositions.length - 1 && 
      this.currMove < this.slidePositions[this.currActive] - ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive++;
    } else if(
      this.currActive > 0 && 
      this.currMove > this.slidePositions[this.currActive] + ((this.windowW / 2) * this.threshold)
    ){
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive--;
    }

    this.currMove = this.slidePositions[this.currActive];
    this.navItems[this.currActive].classList.add("-active");
    TweenLite.to(this.slider, 0.7, {x: this.currMove, onComplete: () => {
      setTimeout(() => {
        this.allVideos.forEach(video => {
          // video.play();
        })
      }, 100)
    }});
  }

  move(e){
    if(!this.pressed) return;

    let x = e.touches[0].clientX,
        readjust = (this.startX - x) * this.momentum;

    x -= this.startMove;

    this.currMove = x - readjust;

    TweenLite.to(this.slider, 0, {x: this.currMove});
  }

  loopThroughProducts(){
    TweenLite.fromTo(this.slideShow, this.timing, {x: "0%"}, {x: "-50%", ease: Power0.easeNone, onComplete: () => {
      this.loopThroughProducts();
    }});
  }
  
  render(){
    const data = this.props.data;

    return(
      <section className="mainSection newPackaging">
        <div className="newPackaging-slider"
          onTouchStart={e => this.down(e)}
          onTouchEnd={() => this.up()}
          onTouchMove={e => this.move(e)}
        >
          <div className="newPackaging-slide">
            <h2 className="heading fadeUp" dangerouslySetInnerHTML={{ __html: data.heading }}/>
            <p className="paragraph newPackaging-paragraphOne fadeUp" dangerouslySetInnerHTML={{ __html: data.para1 }}/>
            <img src="./images/newPackaging/SFF_FS_FOOD_OA_0037_Venison_OH_JG.jpg" alt="" className="mainSection-img newPackaging-img"/>
            <video className="mainSection-video newPackaging-video" autoPlay muted loop>
              <source src="./videos/video2.mp4" type="video/mp4"/>
            </video>
          </div>

          <div className="newPackaging-slide">
            <p className="paragraph newPackaging-paragraphTwo" dangerouslySetInnerHTML={{ __html: data.para2}}/>
            
            <video poster="./images/placeholders/newPackaing.png" className="mainSection-video newPackaging-backgroundSlideShow-video" autoPlay muted loop>
              <source src="./onlineOnly/video7-1.mp4" type="video/mp4"/>
            </video>
          </div>
        </div>

        <div className="newPackaging-nav">
          {data.navItems.map((item, i) => {
            return(
              <div key={`newPackaging-item-${i}`} className={`newPackaging-nav-item ${i === 0 ? "-active" : ""}`} style={{borderColor: "#000"}} onClick={() => this.navClicked(i)}>{item}</div>
            )
          })}
        </div>
      </section>
    )
  }
}


