import React, { Component } from 'react';
import { TweenLite } from "gsap/TweenMax";
import { detect } from 'detect-browser';

const browser = detect();

export default class Squeegie extends Component {
  squeegieIsActive = false;
  bounding = false;
  expanded = false;
  maxSides = 5;
  expandableProps = {};
  statusBarSize = 0;
  expandTiming = 0.6;
  contractTiming = 0.5;
  sideSpacing = 0.09;

  alertTimer = false;
  infoActive = false;

  componentWillMount(){
    if(browser.name === "ios"){
      // this.statusBarSize = 20;
    }
  }

  getBounding(el){
    setTimeout(() => {
    if(el === null) return;
    
    this.container = el.parentNode.parentNode;
    this.containerBounding = this.container.getBoundingClientRect();
    this.windowH = window.innerHeight;
    this.windowW = window.innerWidth;
    this.sliderLeft = this.containerBounding.left - (this.windowW * this.sideSpacing);

    this.squeegieEl = el;
    this.bounding = el.getBoundingClientRect();
    this.lineEl = el.querySelector(".squeegie-line");
    this.slideEls = el.querySelectorAll(".squeegie-slide");
    this.clippedSlideEl = this.slideEls[0];
    this.squeegieChilds = this.squeegieEl.childNodes;
    this.squeegieMountains = el.querySelectorAll(".squeegie-background-mountain");
    this.expandEl = this.squeegieEl.querySelector(".squeegie-expandButton");
    this.closeEl = this.squeegieEl.querySelector(".squeegie-closeButton");
    this.infoButton = this.squeegieEl.querySelector(".squeegie-info-button");

    this.clippedSlideEl.style.zIndex = '2';
    this.clippedSlideEl.style.backgroundColor = '#dadedf';
    this.clippedSlideEl.style.clipPath = `polygon(0% 0%, 50% 0%, 50% 100%, 0% 100%)`;
    this.clippedSlideEl.style.webkitClipPath = `polygon(0% 0%, 50% 0%, 50% 100%, 0% 100%)`;
    this.lineEl.style.left = "50%";

    if(this.props.expandable){
      let windowW = window.innerWidth,
            windowH = window.innerHeight + this.statusBarSize,
            squeegieRatio = this.bounding.width / this.bounding.height;

      // if(windowH === 1044) windowH = 1024;

      const widthRatio = windowW / this.bounding.height,
            heightRatio = windowH / this.bounding.width;

      this.expandableProps.bottom = windowH - this.bounding.top;

      if(widthRatio <= heightRatio){
        this.expandableProps.scale = heightRatio;
      } else {
        this.expandableProps.scale = widthRatio;

        let x = this.expandableProps.bottom / this.expandableProps.scale,
            centerAdjust = x / (widthRatio - squeegieRatio);

        this.expandableProps.x = x + (centerAdjust - x) / 2;
        this.expandableProps.x *= -1;
      }

      this.expandableProps.width = windowH / this.expandableProps.scale;
      this.expandableProps.y = (this.bounding.left - this.sliderLeft) * -1;
      this.expandableProps.x = (windowH - this.bounding.top) * -1;

      this.expandableProps.transform = `
        rotate(-90deg) 
        scale(1)
        translate(${this.expandableProps.x}px, ${this.expandableProps.y}px)
      `;
    }

    this.infoText = this.squeegieEl.parentNode.parentNode.querySelector(".squeegie-info");

    this.squeegieEl.style.width = 100 * this.expandableProps.scale + "%";
    this.squeegieEl.style.height = 100 * this.expandableProps.scale + "%";
    this.squeegieEl.style.transform = `scale(${1 / this.expandableProps.scale})`;
    }, 400);
  }

  mouseUp(){
    this.squeegieIsActive = false;
    this.container.classList.remove("-squeeging");
  }

  mouseDown(){
    this.squeegieIsActive = true;
    this.container.classList.add("-squeeging");
    this.bounding = this.squeegieEl.getBoundingClientRect();
    this.containerBounding = this.container.getBoundingClientRect();
    this.sliderLeft = this.containerBounding.left - (this.windowW * this.sideSpacing);
  }

  squeege(e){
    if(this.squeegieIsActive && this.bounding){
      let x = e.clientX + 1 || e.touches[0].clientX + 1,
          percentMoved = (x - this.bounding.left) / this.bounding.width * 100,
          max = this.maxSides;

      if(this.infoActive && percentMoved > max * 2){
        this.infoActive = false;
  
        this.infoText.classList.remove("-active");
      }

      if(this.expanded){
        x = e.clientY + 1 || e.touches[0].clientY + 1;
        percentMoved = (x - this.expandedBounding.top) / this.expandedBounding.height * 100;
        percentMoved = 100 - percentMoved;
        max += (1 - ((window.innerHeight + this.statusBarSize) / this.expandedBounding.height)) / 2 * 100; // overflow on side when expanded
      }

      if(!this.alertTimer){
        this.alertTimer = true;

        setTimeout(() => {
          this.alertTimer = false;
        }, 5000)
      }

      if(percentMoved > max && percentMoved < 100 - max){
        TweenLite.to(this.lineEl, 0, {left: percentMoved + "%"});

        this.clippedSlideEl.style.clipPath = `polygon(0% 0%, ${percentMoved}% 0%, ${percentMoved}% 100%, 0% 100%)`;
        this.clippedSlideEl.style.webkitClipPath = `polygon(0% 0%, ${percentMoved}% 0%, ${percentMoved}% 100%, 0% 100%)`;
      }
    }
  }

  toggleExpand(){
    this.expanded = !this.expanded;
    this.squeegieEl.classList.toggle("-expanded");
    this.container.classList.toggle("-expanded");
    
    const meatNav = document.querySelector(".meats-nav");
    meatNav.classList.toggle("-active");

    if(this.expanded){
      this.expand();
    } else {
      this.contract();
    }
  }

  expand(){
    document.querySelector(".burger").classList.remove("-active");

    this.squeegieEl.style.transition = 'unset';

    TweenLite.to(this.squeegieMountains, this.expandTiming, {opacity: 0});
    TweenLite.to(this.lineEl, this.expandTiming, {opacity: 0});
    TweenLite.to(this.slideEls, this.expandTiming, {opacity: 0, onComplete: () => {
      // TweenLite.to(this.slideEls, 0, {opacity: 0});
      
      TweenLite.to(this.squeegieEl, this.expandTiming, {
        scale: 5, 
        x: (this.bounding.left - this.sliderLeft) * -1 * this.expandableProps.scale, 
        y: this.bounding.top * -1 * this.expandableProps.scale,
      onComplete: () => {
        this.squeegieEl.style.transform = this.expandableProps.transform;
        this.squeegieEl.style.width = this.windowH + "px";
        this.squeegieEl.style.height = this.windowW + "px";
        
        if(this.expandedBounding === undefined){
          this.expandedBounding = this.squeegieEl.getBoundingClientRect();
        }

        this.closeEl.style.transform = `translateX(${this.expandedBounding.top / this.expandableProps.scale}px)`;
        this.closeEl.style.display = "block";
        if(this.infoButton !== null) this.infoButton.classList.add("-active");
        this.expandEl.style.display = "none";

        TweenLite.to(this.lineEl, 0, {opacity: 1, left: -20});
        TweenLite.to(this.clippedSlideEl, 0, {
          clipPath: `polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%)`,
          webkitClipPath: `polygon(0% 0%, 0% 0%, 0% 100%, 0% 100%)`
        });

        const mountainFade = 300;

        this.squeegieMountains.forEach((mountain, i) => {
          setTimeout(() => {
            TweenLite.to(mountain, this.expandTiming, {opacity: 1});
          }, mountainFade * i)
        })

        setTimeout(() => {
          TweenLite.to(this.slideEls, this.expandTiming, {opacity: 1, onComplete: () => {
            TweenLite.to(this.lineEl, this.expandTiming, {left: 50+"%"});
            TweenLite.to(this.clippedSlideEl, this.expandTiming, {
              clipPath: `polygon(0% 0%, 50% 0%, 50% 100%, 0% 100%)`,
              webkitClipPath: `polygon(0% 0%, 50% 0%, 50% 100%, 0% 100%)`
            });
          }});
        }, this.squeegieMountains.length * mountainFade)
      }});
    }});
  }

  contract(){
    document.querySelector(".burger").classList.add("-active");

    if(this.infoText !== undefined){
      this.infoText.classList.remove("-active");
    }

    TweenLite.to(this.squeegieEl, this.contractTiming, {opacity: 0,  onComplete: () => {
      TweenLite.to(this.squeegieEl, 0, {x: 0, y: 0, scale: 1 / this.expandableProps.scale});
      this.squeegieEl.style.width = 100 * this.expandableProps.scale + "%";
      this.squeegieEl.style.height = 100 * this.expandableProps.scale + "%";

      this.expandEl.style.display = "block";
      this.closeEl.style.display = "none";
      if(this.infoButton !== null) this.infoButton.classList.remove("-active");

      setTimeout(() => {
        TweenLite.to(this.squeegieEl, this.contractTiming, {opacity: 1, onComplete: () => {
          this.squeegieEl.style.transition = null;
        }});
      }, 100)
    }});
  }

  buttons(){
    return(
      <React.Fragment>
        <svg className="squeegie-expandButton" x="0px" y="0px" viewBox="0 0 30.55 30.78" onClick={() => this.toggleExpand()}>
          <g>
            <path className="squeegie-expandButton-path" d="M0.99,29.79l0-16.1l-1-1v17.59c0,0.28,0.22,0.5,0.5,0.5h17.59l-1-1L0.99,29.79z"/>
            <path className="squeegie-expandButton-path" d="M30.05,0H12.46l1,1l16.1,0l0,16.1l1,1V0.5C30.55,0.22,30.33,0,30.05,0z"/>
          </g>
        </svg>

        <svg className="squeegie-closeButton" x="0px" y="0px" viewBox="0 0 40.84 41" onClick={() => this.toggleExpand()}>
          <path className="squeegie-closeButton-path" d="M21.04,20.5L40.7,0.85c0.2-0.2,0.2-0.51,0-0.71c-0.2-0.2-0.51-0.2-0.71,0L20.42,19.71L0.85,0.15
          c-0.2-0.2-0.51-0.2-0.71,0s-0.2,0.51,0,0.71L19.8,20.5L0.15,40.15c-0.2,0.2-0.2,0.51,0,0.71c0.2,0.2,0.51,0.2,0.71,0l19.57-19.57
          l19.57,19.57c0.2,0.2,0.51,0.2,0.71,0c0.2-0.2,0.2-0.51,0-0.71L21.04,20.5z"/>
        </svg>
      </React.Fragment>
    )
  }

  info(){
    return(
      <img src="./images/Info.svg" alt="" className="squeegie-info-button" onClick={this.showInfo.bind(this)}/>
    )
  }

  showInfo(){
    if(this.infoActive){
      this.infoActive = false;

      this.infoText.classList.remove("-active");
    } else {
      TweenLite.to(this.clippedSlideEl, this.expandTiming, {
        clipPath: `polygon(0% 0%, ${this.maxSides}% 0%, ${this.maxSides}% 100%, 0% 100%)`,
        webkitClipPath: `polygon(0% 0%, ${this.maxSides}% 0%, ${this.maxSides}% 100%, 0% 100%)`
      });
      TweenLite.to(this.lineEl, this.expandTiming, {left: this.maxSides + "%", onComplete: () => {
        this.infoText.classList.add("-active");

        this.infoActive = true;
      }});
    }
  }

  render(){
    const data = this.props.data;

    return(
      <div 
        className={`squeegie ${data.classes}`} 

        ref={this.getBounding.bind(this)}

        onMouseDown={() => this.mouseDown()} 
        onMouseUp={() => this.mouseUp()} 
        onMouseMove={e => this.squeege(e)}

        onTouchStart={() => this.mouseDown()} 
        onTouchEnd={() => this.mouseUp()} 
        onTouchMove={e => this.squeege(e)}
      >
        {this.props.children}
        
        <div className="squeegie-line"/>

        {this.props.expandable ? (
          this.buttons()
        ) : (
          null
        )}

        {this.props.info ? (
          this.info()
        ) : (
          null
        )}
      </div>
    )
  } 
}


