import React, { Component } from 'react';
import Meat from './Meat';
import { TweenLite } from "gsap/TweenMax";

export default class Meats extends Component {
  startMove = 0;
  currMove = 0;
  currActive = 0;
  pressed = false;
  momentum = 0.5;
  threshold = 0.2;

  componentDidUpdate(){
    this.meatContain = document.querySelector(".meats");
    this.meats = document.querySelectorAll('.meat');
    this.mainNavList = document.querySelectorAll('.nav-subList-item');
    this.windowW = window.innerWidth;

    this.currActive = this.props.currActive;
    this.currMove = this.currActive * this.windowW * -1;

    if(this.currActive === false) return;

    this.meats.forEach((meat, i) => {
      if(this.windowW > 767){
        meat.style.transform = `translate(calc(${i * 100}vw), -50%)`;
      } else {
        meat.style.transform = `translate(calc(${i * 100}vw), 0%)`;
      }
      
    })

    this.meatContain.style.width = 100 * this.meats.length + "vw";
    TweenLite.to(this.meatContain, 0, {x: this.currMove});

    this.slidePositions = [];
    this.meats.forEach((el, i) => {
      this.slidePositions.push(this.windowW * i * -1);
    })

    this.navItems = document.querySelectorAll(".meats-nav-item");
    this.navItems.forEach(item => item.classList.remove("-active"));
    this.navItems[this.currActive].classList.add("-active");
    this.meats[this.currActive].classList.add("-focused");
    this.mainNavList.forEach(item => item.classList.remove("-active"));
    this.mainNavList[this.currActive].classList.add("-active");
  }

  down(e){
    if(e.target.classList.value.length < 1 || e.target.classList[0].indexOf("squeegie") !== -1) return;
    this.pressed = true;
    this.startX = e.touches[0].clientX;
    this.startMove = this.startX - this.currMove;
  }

  up(){
    this.pressed = false;

    if(
      this.currActive < this.slidePositions.length - 1 && 
      this.currMove < this.slidePositions[this.currActive] - ((this.windowW / 2) * this.threshold)
    ){
      this.mainNavList[this.currActive].classList.remove("-active");
      this.meats[this.currActive].classList.remove("-focused");
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive++;
    } else if(
      this.currActive > 0 && 
      this.currMove > this.slidePositions[this.currActive] + ((this.windowW / 2) * this.threshold)
    ){
      this.mainNavList[this.currActive].classList.remove("-active");
      this.meats[this.currActive].classList.remove("-focused");
      this.navItems[this.currActive].classList.remove("-active");
      this.currActive--;
    }

    this.meats[this.currActive].classList.add("-focused");
    this.currMove = this.slidePositions[this.currActive];
    this.navItems[this.currActive].classList.add("-active");
    this.mainNavList[this.currActive].classList.add("-active");
    TweenLite.to(this.meatContain, 0.4, {x: this.currMove});
  }

  move(e){
    if(!this.pressed) return;

    let x = e.touches[0].clientX,
        readjust = (this.startX - x) * this.momentum;

    x -= this.startMove;

    this.currMove = x - readjust;

    TweenLite.to(this.meatContain, 0.2, {x: this.currMove});
  }

  navClicked(i){
    this.meats[this.currActive].classList.remove("-focused");
    this.navItems[this.currActive].classList.remove("-active");
    this.mainNavList[this.currActive].classList.remove("-active");
    this.currActive = i;
    this.currMove = this.slidePositions[this.currActive];
    this.meats[this.currActive].classList.add("-focused");
    this.navItems[this.currActive].classList.add("-active");
    this.mainNavList[this.currActive].classList.add("-active");
    TweenLite.to(this.meatContain, 0.4, {x: this.currMove});
  }
  
  render(){
    const data = this.props.data;

    return(
      <React.Fragment>
        <div className="meats" 
          onTouchStart={e => this.down(e)}
          onTouchEnd={() => this.up()}
          onTouchMove={e => this.move(e)}
        >
          {data.packets.map((meat, i) => {
            return <Meat data={ meat } lang={ this.props.lang } key={`meat-${i}`}/>
          })}
        </div>
        
        {data.navItems ? (
          <div className="meats-nav">
            {data.navItems.map((navItem, i) => {
              return <div key={`meat-item-${i}`} className="meats-nav-item" style={{borderColor: navItem.color}} onClick={() => this.navClicked(i)}>{navItem.name}</div>
            })}
          </div>
        ) : (
          null
        )}
      </React.Fragment>
    )
  }
}


